import { useCallback, useEffect } from "react"
import { RPTooltip } from "@rp/react/web/common/base-components"
// Components
import { IconButton, Typography } from "@rp/react/web/mui/core"
import { KeyboardArrowLeft, KeyboardArrowRight } from "@rp/react/web/mui/icons"
// Styles
import { MatchesSelectorContainer } from "./MatchesSelector.styles"

type Props = {
  matchesCount: number
  currentMatch: number
  onSelectionChange?: (index: number) => void
}

const MatchesSelector = ({ matchesCount, onSelectionChange, currentMatch }: Props) => {
  const selectNextIndex = useCallback(() => {
    const index = currentMatch + 1 === matchesCount ? 0 : currentMatch + 1

    onSelectionChange?.(index)
  }, [currentMatch, matchesCount, onSelectionChange])

  const selectPreviousIndex = useCallback(() => {
    const index = currentMatch - 1 === -1 ? matchesCount - 1 : currentMatch - 1

    onSelectionChange?.(index)
  }, [currentMatch, matchesCount, onSelectionChange])

  const handleKeydown = useCallback(
    (e: KeyboardEvent) => {
      if (e.key === "ArrowLeft") {
        e.preventDefault()
        selectPreviousIndex()
      }

      if (e.key === "ArrowRight") {
        e.preventDefault()
        selectNextIndex()
      }
    },
    [selectNextIndex, selectPreviousIndex],
  )

  // On mount
  useEffect(() => {
    window.addEventListener("keydown", handleKeydown)

    return () => window.removeEventListener("keydown", handleKeydown)
  }, [handleKeydown])

  return (
    <MatchesSelectorContainer>
      <Typography variant="body2" fontWeight={700}>
        {matchesCount} {matchesCount === 1 ? "MATCH" : "MATCHES"}
      </Typography>
      <RPTooltip title="Previous" placement="top" enterDelay={300}>
        <IconButton onClick={selectPreviousIndex}>
          <KeyboardArrowLeft />
        </IconButton>
      </RPTooltip>
      <Typography variant="body2">
        {currentMatch + 1}/{matchesCount}
      </Typography>
      <RPTooltip title="Next" placement="top" enterDelay={300}>
        <IconButton onClick={selectNextIndex}>
          <KeyboardArrowRight />
        </IconButton>
      </RPTooltip>
    </MatchesSelectorContainer>
  )
}

export { MatchesSelector }
